<template>
  <div class="container">
    <div class="histogram-con">
      <div class="selector">
        <div class="input-item">
          <label>区域筛选</label>
          <div class="selecteds" v-for="(item,index) in selectedList" :key="index">
            <span>{{item}}</span>
            <div class="closeSelected">
              <img
                @click="event().toDeleteProvince(index)"
                style="cursor: pointer;"
                width="10px"
                src="@/assets/images/symbols-icons-light-close.png"
                alt
              >
            </div>
          </div>
          <div class="icon" v-if="!showInput">
            <img width="100%" @click="showInput=true" src="@/assets/images/add.png" alt>
          </div>
          <div class="input" v-if="showInput">
            <input
              v-model="province"
              type="text"
              :placeholder="selectTxt"
              @keyup.enter="event().selectedProvince()"
            >
            <img
              @click="event().selectedProvince()"
              style="margin:0 5px;cursor: pointer;"
              width="25px"
              src="@/assets/images/symbols-icons-light-check.png"
              alt
            >
            <img
              @click="showInput=false"
              style="cursor: pointer;"
              width="14px"
              src="@/assets/images/symbols-icons-light-close.png"
              alt
            >
          </div>
        </div>
      </div>
      <div id="myBarChart" ref="barCharts" class="map"></div>
      <div style="margin-top:-20px;margin-left:85px;">
        <p style="color:#D5EEFF;">WHO指导值({{this.whoMax}} μg/m³)</p>
        <p style="color:#95E2FE;">中国国家标准({{this.chinaMax}} μg/m³)</p>
      </div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
// var XLSX = require("xlsx");
// 引入基本模板
import { mapState } from "vuex";
import { GRID } from "@/jslib/areaAddress";
var echarts = require("echarts");
// import "echarts/lib/component/title";
// import "echarts/lib/component/legend";
import "echarts/lib/chart/heatmap";
import "echarts/lib/component/toolbox";
import "echarts/lib/component/tooltip";
// import areaData from "@/jslib/areaData";
export default {
  data() {
    return {
      showInput: false,
      selectTxt: "输入省份进行筛选",
      whoMin: 0,
      whoMax: 10, //who最大的也就是中国最小的
      chinaMax: 35,
      province: "",
      selectedList: [],
      echartsOption: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        dataZoom: [
          {
            type: "slider",
            show: true,
            handleSize: 0, //滑动条的 左右2个滑动条的大小
            handleColor: "#32ADFE", //h滑动图标的颜色
            backgroundColor: "#D5EEFF", //两边未选中的滑动条区域的颜色
            fillerColor: "#32ADFE",
            yAxisIndex: [0],
            start: 0, //数据窗口范围的起始百分比
            end: 2
          },
          {
            type: "inside",
            start: 0, //数据窗口范围的起始百分比
            end: 2,
            yAxisIndex: [0],
            zoomOnMouseWheel: false,
            moveOnMouseWheel: true
          }
        ],
        grid: {
          left: "5%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          name: "单位：ug/m3",
          nameLocation: "center",
          nameGap: 23,
          type: "value",
          boundaryGap: [0, 0.01],
          min: 0,
          max: 200,
          splitNumber: 20,
          splitLine: {
            show: true,
            lineStyle: {
              color: "#fff"
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: ["#EBEBEB", "#EBEBEB"]
            }
          }
          // axisLabel:{
          //   formatter:'{value} ug/m3'
          // }
        },
        yAxis: {
          type: "category",
          axisLabel: {
            // interval: 0, // 0 强制显示所有，1为隔一个标签显示一个标签，2为隔两个
            // rotate: 45, //标签旋转角度，在标签显示不下的时可通过旋转防止重叠
            formatter: function(value) {
              return "{label|" + value + "}";
            },
            rich: {
              label: {
                align: "right",
                padding: [0, 0, 0, 10],
                width: 50
              }
            }
          },
          data: []
        },
        color: [
          "#46ADF2",
          "#0CBCEB",
          "#19cde2",
          "#1fdfd9",
          "#15ebcd",
          "#17f6bb"
        ],
        itemStyle: {
          barBorderRadius: [0, 10, 10, 0]
        },
        series: [],
        animation: false
      },
      allCitiesArray: [],
      citiesArray: [],
      yearsAndDataArray: [],
      newPolicies: [],
      pollutantsArray: ["CO", "NO2", "O3", "PM10", "PM2d5", "SO2"],
      yearsArray: ["2013", "2014", "2015", "2016", "2017"],
      citiesAndPollutantsData: {}, // 污染物总数据
      provinceAndPollutantsData: {},
      hotCityData: {},
      limit: 400, // TODO: 调整城市数量限制
      pollutantsAndAllCities: {},
      pollutantsAndAllProvince: {},
      pollutantProvinceCity: {},
      policiesArray: [
        "GT",
        "SN",
        "GL",
        "HBC",
        "CC",
        "PG",
        "EI",
        "EV",
        "OV",
        "PHBC"
      ],
      policyCities: [], // 政策总数据
      policiesAndAllCities: [],
      policyProvinces: {},
      areaHotCityData: {}
    };
  },
  created() {
    if (this.isIE()) {
      this.echartsOption.toolbox = {
        show: true,
        showTitle: true,
        orient: "vertical",
        right: "20px",
        top: "center",
        feature: {
          saveAsImage: { show: true }
        }
      };
    }
  },
  mounted() {
    this.initData();

    this.drawMap();
    this.switchArea(0);
    if (this.chartData.tab === 1) {
      this.switchPollutant(0);
      // this.putChartOptions()
      this.refreshChart(this.chartData);
    }
  },
  computed: {
    ...mapState([
      "lang",
      "PM2d5",
      "CO",
      "NO2",
      "PM10",
      "SO2",
      "O3",
      "policy",
      "provinceAir",
      "cityData",
      "chartData",
      "importantCity",
      "hotCity"
    ]),
    returnChartDataObj() {
      let newChartDataObj = {};
      newChartDataObj.area = this.chartData.area;
      newChartDataObj.tab = this.chartData.tab;
      newChartDataObj.heatYear = this.chartData.heatYear;
      newChartDataObj.barchartYear = this.chartData.barchartYear;
      newChartDataObj.policy = this.chartData.policy;
      newChartDataObj.pollutant = this.chartData.pollutant;
      newChartDataObj.charType = this.chartData.charType;
      return newChartDataObj;
    },
    returunLang() {
      return this.lang;
    }
  },
  watch: {
    returunLang(v, ov) {
      if (v === "en") {
        this.echartsOption.xAxis.name = "Unit: ug/m3";
        this.drawMap();
      } else {
        this.echartsOption.xAxis.name = "单位：ug/m3 ";
        this.drawMap();
      }
    },
    returnChartDataObj(newValue, oldValue) {
      if (newValue.area !== oldValue.area) {
        this.province = "";
        this.selectedList = [];
        this.showInput = false;
      }
      if (newValue.charType===1) {
        this.refreshChart(newValue, oldValue);
      }
      if (newValue.tab === 1) {
        this.refreshChart(newValue, oldValue);
        // this.echartsOption.xAxis.min = 0;
        // this.echartsOption.xAxis.max = 200;
      }
      if (newValue.tab === 0) {
        // this.getPolicyData(this.chartData.policy)
        console.log(newValue.charType);
        let yearsArray = newValue.barchartYear.map(number => {
          switch (number) {
            case 0:
              return 2013;
              break;
            case 1:
              return 2014;
              break;
            case 2:
              return 2015;
              break;
            case 3:
              return 2016;
              break;
            case 4:
              return 2017;
              break;
            case 5:
              return 2018;
              break;
            default:
              break;
          }
        });
        console.log(yearsArray);
        // debugger
        this.filterYear(yearsArray);
        this.choosePolicy(newValue.policy);
        this.switchArea(newValue.area);
      }
    }
  },
  methods: {
    isIE() {
      if (!!window.ActiveXObject || "ActiveXObject" in window) {
        return true;
      } else {
        return false;
      }
    },
    getPolicyData(type, importantCity, hcObj) {
      importantCity = importantCity || [];
      let newHcObj = hcObj;
      if (newHcObj) {
        newHcObj = JSON.parse(newHcObj);
      }
      console.log(newHcObj);
      let tempArray = [];
      let policyData = [];
      let area = [];
      if (importantCity.length > 0) {
        //如果当前选择的是重点城市
        this.yearsArray.forEach((year, index) => {
          policyData = [];
          tempArray.push({
            name: `${year}年`,
            type: "bar",
            barWidth: 10,
            data: []
          });
          this.policy[type].forEach((item, cityIndex) => {
            importantCity.forEach(importantCityItem => {
              if (item["城市"] === importantCityItem["城市"]) {
                if (item[year] === undefined) {
                  item[year] = 0;
                }
                policyData.push(item[year]);
                if (index === 0) {
                  //地区城市名只需要遍历一次即可
                  area.push(item["城市"]);
                }
              }
            });
          });
          tempArray[index].data = policyData;
        });
        console.log(area);
        this.yearsAndDataArray = tempArray;
        this.echartsOption.yAxis.data = area;
        this.echartsOption.series = tempArray;
      } else if (newHcObj) {
        this.yearsArray.forEach((year, index) => {
          policyData = [];
          tempArray.push({
            name: `${year}年`,
            type: "bar",
            barWidth: 10,
            data: []
          });
          this.policy[type].forEach((item, cityIndex) => {
            newHcObj.hc.forEach(hcItem => {
              if (hcItem["区域"] === newHcObj.hcName) {
                if (hcItem["城市"] === item["城市"]) {
                  if (item[year] === undefined) {
                    item[year] = 0;
                  }
                  policyData.push(item[year]);
                  if (index === 0) {
                    //地区城市名只需要遍历一次即可
                    area.push(item["城市"]);
                  }
                }
              }
            });
          });
          tempArray[index].data = policyData;
        });
        this.yearsAndDataArray = tempArray;
        console.log(area);
        this.echartsOption.yAxis.data = area;
        this.echartsOption.series = tempArray;
      } else {
        this.yearsArray.forEach((year, index) => {
          policyData = [];
          tempArray.push({
            name: `${year}年`,
            type: "bar",
            barWidth: 10,
            data: []
          });
          this.policy[type].forEach((item, cityIndex) => {
            if (item[year] === undefined) {
              item[year] = 0;
            }
            policyData.push(item[year]);
            if (index === 0) {
              //地区城市名只需要遍历一次即可
              if (item["城市"]) {
                area.push(item["城市"]);
              } else if (item["地区"]) {
                area.push(item["地区"]);
              }
            }
          });
          tempArray[index].data = policyData;
        });
        this.yearsAndDataArray = tempArray;
        this.echartsOption.yAxis.data = area;
        this.echartsOption.series = tempArray;
      }
      this.myChart.setOption(this.echartsOption, true);
    },
    drawMap() {
      this.myChart = echarts.init(document.getElementById("myBarChart"));
      this.myChart.setOption(this.echartsOption);
      let _this = this;
      setTimeout(function() {
        window.onresize = function() {
          _this.myChart.resize();
        };
      }, 200);
    },
    event() {
      return {
        selectedProvince: () => {
          if (this.province === "") {
            alert("筛选条件不能为空！");
            return;
          }
          //需要过滤重复
          let provideData = this.province;
          if (provideData.indexOf("省") !== -1) {
            provideData = provideData.substring(0, provideData.length - 1);
            if (this.selectedList.includes(provideData)) {
              alert("不能重复筛选");
              return;
            }
            this.selectedList.push(provideData);
          } else if (provideData.indexOf("市") !== -1) {
            provideData = provideData.substring(0, provideData.length - 1);
            if (this.selectedList.includes(provideData)) {
              alert("不能重复筛选");
              return;
            }
            this.selectedList.push(provideData);
          } else {
            if (this.selectedList.includes(provideData)) {
              alert("不能重复筛选");
              return;
            }
            this.selectedList.push(provideData);
          }
          // 城市
          this.citiesArray = this.selectedList;

          this.echartsOption.dataZoom[0].end = 100;
          if (this.chartData.tab === 1) {
            this.switchPollutant(this.chartData.pollutant);
          }

          let yearsArray = this.chartData.barchartYear.map(number => {
            switch (number) {
              case 0:
                return 2013;
                break;
              case 1:
                return 2014;
                break;
              case 2:
                return 2015;
                break;
              case 3:
                return 2016;
                break;
              case 4:
                return 2017;
                break;
              case 5:
                return 2018;
                break;

              default:
                break;
            }
          });
          // debugger
          this.filterYear(yearsArray);
          // }
          this.putChartOptions();
        },
        toDeleteProvince: index => {
          this.selectedList.splice(index, 1);
          if (this.selectedList.length === 0) {
            this.echartsOption.dataZoom[0].end = 2;
          }
          if (this.chartData.tab === 1) {
            this.refreshChart(this.chartData);
          }
        }
      };
    },
    returnChartNode() {
      return this.$refs.barCharts;
    },
    network() {
      return {};
    },
    handler() {
      return {
        //整理政策进展数据
        resetData: type => {
          let newCityData = {};
          let allCity = [];
          let keys = [];
          newCityData[type] = [];
          this.yearsArray.forEach(y => {
            keys.push(type + y);
          });
          this.cityData.forEach((item, index) => {
            if (index < this.yearsArray.length) {
              newCityData[type].push({
                year: this.yearsArray[index],
                data: item[keys[index]] || 0
              });
            }
          });
        }
      };
    },
    initData() {
      // 用来存放主要数据
      this.citiesAndPollutantsData = {};
      this.provinceAndPollutantsData = {};
      this.pollutantsArray.forEach(pollutant => {
        // 把 vuex 里的几个以 pollutant 为 key 的数据 forEach 一遍
        if (this.chartData.area === 0) {
          this.provinceAir[pollutant].forEach(item => {
            //this.provinceAip.CO...
            //             // 处理主要数据
            if (!this.provinceAndPollutantsData[item["省份"]]) {
              this.provinceAndPollutantsData[item["省份"]] = {};
            }

            if (!this.provinceAndPollutantsData[item["省份"]][pollutant]) {
              this.provinceAndPollutantsData[item["省份"]][pollutant] = [];
            }
            this.yearsArray.forEach(year => {
              this.provinceAndPollutantsData[item["省份"]][pollutant].push({
                year,
                data: item[year] || 0
              });
            });
          });
        }

        this[pollutant].forEach(item => {
          // 处理 pollutant -> province -> city 的数据

          if (!this.pollutantProvinceCity[pollutant]) {
            this.pollutantProvinceCity[pollutant] = {};
          }

          if (!this.pollutantProvinceCity[pollutant][item["省份"]]) {
            this.pollutantProvinceCity[pollutant][item["省份"]] = [];
          }

          this.pollutantProvinceCity[pollutant][item["省份"]].push(
            item["城市"]
          );

          // 处理主要数据
          if (!this.citiesAndPollutantsData[item["城市"]]) {
            this.citiesAndPollutantsData[item["城市"]] = {};
          }

          if (!this.citiesAndPollutantsData[item["城市"]][pollutant]) {
            this.citiesAndPollutantsData[item["城市"]][pollutant] = [];
          }
          // this.citiesAndPollutantsData['深圳']['PM2d5']

          this.yearsArray.forEach(year => {
            this.citiesAndPollutantsData[item["城市"]][pollutant].push({
              year,
              data: item[year] || 0
            });
          });
        });
        this.pollutantsAndAllProvince[pollutant] = this.provinceAir[
          pollutant
        ].map(i => i["省份"]);
        this.pollutantsAndAllCities[pollutant] = this[pollutant].map(
          i => i["城市"]
        );
      });

      // 数据处理完了
      // 不你没有
      // 开始处理 hot city
      this.hotCityData = {};
      this.hotCity.forEach(item => {
        if (!this.hotCityData[item["区域"]]) {
          this.hotCityData[item["区域"]] = [];
        }

        this.hotCityData[item["区域"]].push(item["城市"]);
      });

      //政策进展
    },
    getPollutantData(type) {
      let tempArray = [];
      this.yearsArray.forEach(year => {
        // let itemOfTheYear = this.yearsAndDataArray.find(i => i.name === `${year}年`)
        // if (!itemOfTheYear) {
        // TODO: init this.yearsAndDataArray
        tempArray.push({
          name: `${year}年`,
          type: "bar",
          barWidth: 10,
          data: []
        });
        // }
      });
      this.yearsAndDataArray = tempArray;

      // TODO: 调整城市数量限制
      this.citiesArray = this.citiesArray.slice(0, this.limit);
      if (this.chartData.area === 0) {
        this.citiesArray.forEach(city => {
          if (!this.provinceAndPollutantsData[city][type]) {
            // 如果没有这个城市，直接每个 yearsAndDataArray push 一个 0
            this.yearsAndDataArray.forEach(i => {
              i.data.push(0);
            });
            return;
          }
          this.provinceAndPollutantsData[city][type].forEach(item => {
            // debugger
            this.yearsAndDataArray
              .find(i => i.name === `${item.year}年`)
              .data.push(item.data);
          });
        });
      } else {
        this.citiesArray.forEach(city => {
          if (!this.citiesAndPollutantsData[city][type]) {
            // 如果没有这个城市，直接每个 yearsAndDataArray push 一个 0
            this.yearsAndDataArray.forEach(i => {
              i.data.push(0);
            });
            return;
          }
          this.citiesAndPollutantsData[city][type].forEach(item => {
            // debugger
            this.yearsAndDataArray
              .find(i => i.name === `${item.year}年`)
              .data.push(item.data);
          });
        });
      }
    },
    choosePolicy(type) {
      switch (type) {
        case "CC":
          this.echartsOption.dataZoom[0].end = 30;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "PG":
          this.echartsOption.dataZoom[0].end = 30;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "EI":
          this.echartsOption.dataZoom[0].end = 30;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "EV":
          this.echartsOption.dataZoom[0].end = 2;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "GT":
          this.echartsOption.dataZoom[0].end = 2;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "SN":
          this.echartsOption.dataZoom[0].end = 2;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "GL":
          this.echartsOption.dataZoom[0].end = 2;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "OV":
          this.echartsOption.dataZoom[0].end = 2;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "HBC":
          this.echartsOption.dataZoom[0].end = 2;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
        case "PHBC":
          this.echartsOption.dataZoom[0].end = 2;
          this.echartsOption.xAxis.max = null;
          this.chooseArea(this.chartData.area);
          break;
      }
    },
    chooseArea(type) {
      if (type === "我全都要") {
        if (this.chartData.tab === 1) {
          let pollutant = this.findPollutant(this.chartData.pollutant);
          this.citiesArray = this.pollutantsAndAllCities[pollutant];
          return
        }
        if (this.chartData.tab === 0) {
          this.getPolicyData(this.chartData.policy);
        }
      }
      if (type === "我要省份") {
        if (this.chartData.tab === 1) {
          let pollutant = this.findPollutant(this.chartData.pollutant);
          this.citiesArray = this.pollutantsAndAllProvince[pollutant];
          return
        }
        if (this.chartData.tab === 0) {
          this.getPolicyData(this.chartData.policy);
        }
        
      }
      if (type === "我很重要") {
        this.citiesArray = this.importantCity.map(i => i["城市"]);
        return
        if (this.chartData.tab === 0) {
          this.getPolicyData(this.chartData.policy, this.importantCity);
        }
      }
       if (this.chartData.tab === 0) {
        if (type === "京津冀") {
            let value = { hc: this.hotCity, hcName: "京津冀" };
            let valueStr = JSON.stringify(value);
            this.getPolicyData(this.chartData.policy, [], valueStr);
        }
        if (type === "长三角") {
            let value = { hc: this.hotCity, hcName: "长三角" };
            let valueStr = JSON.stringify(value);
            this.getPolicyData(this.chartData.policy, [], valueStr);
        }
        if (type === "珠三角") {
            let value = { hc: this.hotCity, hcName: "珠三角" };
            let valueStr = JSON.stringify(value);
            this.getPolicyData(this.chartData.policy, [], valueStr);
        }
       }
      if (this.chartData.tab === 1) {
        this.citiesArray = this.hotCityData[type];
        return
      }
      // if (this.chartData.tab === 0) {
      //   this.citiesArray = this.areaHotCityData[type];
      // }
    },
    switchArea(number) {
      switch (number) {
        case 0:
            this.echartsOption.dataZoom[0].end = 16;
          this.selectTxt = "输入省份进行筛选";
          this.chooseArea("我要省份");
          break;
        case 1:
            this.echartsOption.dataZoom[0].end = 1.5;
          this.selectTxt = "输入城市进行筛选";
          this.chooseArea("我全都要");
          break;
        case 2:
          this.echartsOption.dataZoom[0].end = 7;
          this.selectTxt = "输入城市进行筛选";
          this.chooseArea("我很重要");
          break;
        case 3:
            this.echartsOption.dataZoom[0].end = 35;
          this.selectTxt = "输入城市进行筛选";
          this.chooseArea("京津冀");
          break;
        case 4:
          this.echartsOption.dataZoom[0].end =20;
          this.selectTxt = "输入城市进行筛选";
          this.chooseArea("长三角");
          break;
        case 5:
          this.echartsOption.dataZoom[0].end = 60;
          this.selectTxt = "输入城市进行筛选";
          this.chooseArea("珠三角");
          break;
        default:
          break;
      }
    },
    findPollutant(number) {
      switch (number) {
        case 0:
          this.whoMax = 10;
          this.chinaMax = 35;
          this.echartsOption.xAxis.max = 200;
          return "PM2d5";
          break;
        case 1:
          this.whoMax = 40;
          this.chinaMax = 40;
          this.echartsOption.xAxis.max = 80;
          return "NO2";
          break;
        case 2:
          this.whoMax = 20;
          this.chinaMax = 70;
          this.echartsOption.xAxis.max = 350;
          return "PM10";
          break;
        case 3:
          this.whoMax = 100;
          this.chinaMax = 160;
          this.echartsOption.xAxis.max = 240;
          return "O3";
          break;
        case 4:
          this.whoMax = 0;
          this.chinaMax = 60;
          this.echartsOption.xAxis.max = 120;
          return "SO2";
          break;
        case 5:
          this.whoMax = 0;
          this.chinaMax = 4;
          this.echartsOption.xAxis.max = 8;
          return "CO";
          break;
        default:
          break;
      }
    },
    switchPollutant(number) {
      let pollutant = this.findPollutant(number); //查询污染物名称
      this.getPollutantData(pollutant); //查询污染物数据
    },
    filterYear(yearFilterArray) {
      this.echartsOption.series = this.yearsAndDataArray.filter(i =>
        yearFilterArray.some(year => `${year}年` === i.name)
      );
      // debugger
    },
    putChartOptions() {
      // TODO: 调整城市数量限制
      this.citiesArray = this.citiesArray.slice(0, this.limit);

      this.echartsOption.yAxis.data = this.citiesArray;

      this.echartsOption.series.unshift({
        type: "bar",
        markArea: {
          data: [
            [
              {
                itemStyle: {
                  color: "#D5EEFF"
                },
                xAxis: 0
              },
              {
                xAxis: this.whoMax
              }
            ],
            [
              {
                // name: `中国国家标准(${this.chinaMax}μg/m³)`,
                itemStyle: {
                  color: "#95E2FE"
                },
                xAxis: this.whoMax
              },
              {
                xAxis: this.chinaMax
              }
            ]
          ]
        },
        data: []
      });
      if (this.chartData.tab === 0) {
        this.echartsOption.series[0].markArea = null;
      }
      this.myChart.setOption(this.echartsOption, true); // https://ecomfe.github.io/echarts-doc/public/en/api.html#echartsInstance.setOption
    },
    refreshChart(newValue, oldValue) {
      if (newValue.area !== 0 && newValue.area !== 1) {
        // 如果不是 身份 城市 ，就清除 selectedList
        this.selectedList = [];
      }

      if (this.selectedList.length <= 0) {
        // 如果有筛选省或市，就不要走 switchArea
        // debugger
        // if (newValue.pollutant !== oldValue.pollutant) {
        this.switchArea(newValue.area);
      } else {
        // 待优化
        let pollutant = this.findPollutant(this.chartData.pollutant);
        if (newValue.area === 0) {
          this.citiesArray = this.selectedList.reduce((prev, next) => {
            return [...prev, ...this.pollutantProvinceCity[pollutant][next]];
          }, []);
        }
        if (newValue.area === 1) {
          this.citiesArray = this.selectedList;
        }
      }
      this.switchPollutant(newValue.pollutant);

      let yearsArray = newValue.barchartYear.map(number => {
        switch (number) {
          case 0:
            return 2013;
            break;
          case 1:
            return 2014;
            break;
          case 2:
            return 2015;
            break;
          case 3:
            return 2016;
            break;
          case 4:
            return 2017;
            break;
          case 5:
            return 2018;
            break;

          default:
            break;
        }
      });

      // debugger
      this.filterYear(yearsArray);
      // }

      this.putChartOptions();
    }
  }
};
</script>

